ul.navigation {
  position: fixed;
  top: 50%;
  left: 50%;
  background: #fff;
  height: 100%;
  width: 100%;
  transform: translate(-50%, -50%);
}
ul.navigation li {
  list-style: none;
  position: relative;
  text-align: center;
  overflow: hidden;
}
ul.navigation li::before {
  content: "";
  position: absolute;
  top: 50%;
  left: -100%;
  transform: translateY(-50%);
  width: 100%;
  height: 3px;
  background: #262626;
  transition: 0.5s;
}
ul.navigation li:hover::before {
  left: 100%;
}
ul.navigation li a {
  position: relative;
  display: inline-block;
  color: #fff;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 50px;
  padding: 20px 40px;
}
ul.navigation li a::before,
ul.navigation li a::after {
  content: attr(data);
  position: absolute;
  top: 0;
  left: 0;
  padding: 20px 40px;
  color: #262626;
  transition: 0.9s;
  transition-delay: 0.3s;
}
ul.navigation li a::before {
  clip-path: polygon(0 0, 100% 0, 100% 50%, 0 50%);
}
ul.navigation li a::after {
  clip-path: polygon(0 50%, 100% 50%, 100% 100%, 0 100%);
}
ul.navigation li:hover a::before {
  top: -3px;
  left: 7px;
  color: #ff5722;
}
ul.navigation li:hover a::after {
  top: 3px;
  left: -7px;
  color: #ff5722;
}

